/* ============
	 RADIO
============== */

//move radio options inline
.js-webform-radios{
	margin-bottom: 0;
	@include safeFlex(row,wrap,null,null);
}


//move input inline with label
.js-form-type-radio{
	width: auto;
	margin-right: 1em;
	label,input{
		display: inline-block;
	}
	label{
		font-weight: $regular;
		margin-bottom: 0;
	}
	input[type="radio"]{
		margin-right: .3em;
		position: relative;
		top: -2px;
	}
	//custom radio buttons (defined in forms.scss, set with variable from macros.twig)
	.custom-options &{
		@include customOptions;
		label:before{
			border-radius: 50%;
		}
	}
}