================= */
body {
  background: #ffffff;
}

body > .overflow-guard {
  overflow-x: hidden;
}

body div {
  box-sizing: border-box;
}

body div * {
  box-sizing: inherit;
}

.container-inline div {
  display: inline;
}

/* MAP PINS
-------------- */
.gm-style-iw strong {
