  font-size: 14px;
}

/* PANELS-IPE
------------------- */
#panels-ipe-tray {
  z-index: 1000;
}

#panels-ipe-tray a {
  padding: 5px 10px;
  border: 1px solid;
}

#panels-ipe-tray a.panelizer-ipe-save-custom {
  border-color: #218d00;
  background: #218d00;
  color: #fff;
}

#panels-ipe-tray a.panelizer-ipe-save-custom:hover, #panels-ipe-tray a.panelizer-ipe-save-custom:focus {
  background: transparent;
  color: #155a00;
}

