}

.ipe-category-picker-bottom {
  white-space: initial;
}

/* SKIP NAV
------------- */
.overflow-guard > a.focusable:focus {
  position: fixed !important;
  top: 0;
  left: 0;
  z-index: 9999;
  display: block;
  width: 100%;
  background: #fff;
  padding: .25em 1em;
}

/* KINTING
-------------- */
.kint {
  position: fixed;
  z-index: 9999;
  top: 200px;
  right: 5%;
  width: 90%;
  max-width: 800px;
  max-height: -webkit-calc(90% - 200px);
  max-height: -moz-calc(90% - 200px);
  max-height: calc(90% - 200px);
  overflow: scroll;
}

/* BACK TO TOP
----------------- */
.back-anchor {
  display: none;
  position: fixed;
  bottom: 3%;
  right: 3%;
  box-shadow: 3px 3px 5px rgba(35, 35, 35, 0.35);
  z-index: 9999;
  border-radius: 50%;
}

.back-anchor a {
  display: block;
  width: 50px;
  height: 50px;
  background: rgba(35, 35, 35, 0.85);
  border-radius: 50%;
  text-indent: -300vw;
  position: relative;
}

.back-anchor a:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  background: url("svg/sprite.css.svg") no-repeat;
  background-position: 40% 25%;
  background-size: 180px 150px;
}

/*  FILE ICONS
--------------- */
a[href$='.pdf'] {
  position: relative;
  padding-left: 22px;
}

a[href$='.pdf']:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 4px;
  width: 15px;
  height: 18px;
  background: url("svg/sprite.css.svg") no-repeat;
  background-position: 20% 75%;
  background-size: 108px 90px;
}

a[href$='.pdf']:hover:before, a[href$='.pdf']:focus:before {
  opacity: 0.7;
  filter: Alpha(opacity=70);
}

